import $ from './jquery';

$(() => {
  $('.menu-section a.bg-hover').hover(({ target }) => {
    const sectionName = $(target).attr('name');

    $('.cover-background-image.default').removeClass('active');
    $(`.cover-background-image[name=${sectionName}]`).addClass('active');
  }, () => {
    $('.cover-background-image').removeClass('active');
    $('.cover-background-image.default').addClass('active');
  });
});
